import HeadterBtn from "./Button/LinkBtn";
import LogoutBtn from "./Button/LogoutBtn";
import { Root, Content } from "../assets/css/HeaderStructure";
import { useEffect, useState } from "react";
import { userAuth } from "../utils/userAuth";

function HeaderComponents() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    async function checkLoginStatus() {
      const loggedIn = await userAuth();
      setIsLoggedIn(loggedIn);
    }

    checkLoginStatus();
  }, []); //    componentDidMount    

  return (
    <Root>
      <Content>
        <HeadterBtn url="/" title="HOME" />
        <HeadterBtn url="/market/sell" title="" />
        <HeadterBtn url="/community/free" title="" />
        <HeadterBtn url="/shop/search" title=" " />
        {isLoggedIn && <HeadterBtn url="/mypage/my_post" title="" />}
        {isLoggedIn && <LogoutBtn url="/" title="" />}
        {!isLoggedIn && <HeadterBtn url="/login" title="" />}
      </Content>
    </Root>
  );
}

export default HeaderComponents;
